// Regular
@font-face {
  font-family: 'Raleway';
  font-style: normal;
  font-weight: 400;
  src: url('../fonts/raleway-v13-latin-regular.eot');
  /* IE9 Compat Modes */
  src: local('Raleway'), local('Raleway-Regular'),
    url('../fonts/raleway-v13-latin-regular.eot?#iefix') format('embedded-opentype'),
    /* IE6-IE8 */
    url('../fonts/raleway-v13-latin-regular.woff2') format('woff2'),
    /* Super Modern Browsers */
    url('../fonts/raleway-v13-latin-regular.woff') format('woff'),
    /* Modern Browsers */
    url('../fonts/raleway-v13-latin-regular.ttf') format('truetype'),
    /* Safari, Android, iOS */
    url('../fonts/raleway-v13-latin-regular.svg#Raleway') format('svg');
  /* Legacy iOS */
}

